declare var $: any
declare var QRCode:any
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events, ToastController } from 'ionic-angular';
import { HttpServicesProvider } from '../../../../../providers/http-services';
// import { Utils } from '../../../../../providers/Utils';
import { StorageProvider } from '../../../../../providers/storage';
import { PhoneDeviceProvider } from '../../../../../providers/phone-device';

/**
 * Generated class for the LmtagfiletechdatarecordinfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmtagfiletechdatarecordinfo',
  templateUrl: 'lmtagfiletechdatarecordinfo.html',
})
export class LmtagfiletechdatarecordinfoPage {
  public formdata = [
    { 'name': '检修任务', 'index': 'projectname', 'type': 'text', 'placeholder': '请输入检修任务' },
    { 'name': '主要工作内容', 'index': 'projectdesc', 'type': 'textarea', 'placeholder': '请输入工作内容', 'disabled': true, 'autoheight': true },
  ]

  public formdata2 = [
    { 'name': '测量人', 'index': 'measuremanname', 'type': 'selectUser', 'indexId': 'measureman', 'placeholder': '请输入测量人' },
    { 'name': '测量时间', 'index': 'measuretime', 'type': 'datetime', 'format': 'YYYY-MM-DD HH:mm:ss', 'placeholder': '请输入测量时间' },
    { 'name': '记录人', 'index': 'recordmanname', 'type': 'selectUser', 'indexId': 'recordman', 'placeholder': '请输入记录人' },
    { 'name': '记录时间', 'index': 'recordtime', 'type': 'datetime', 'format': 'YYYY-MM-DD HH:mm:ss', 'placeholder': '请输入记录时间' },
    { 'name': '审核人', 'index': 'auditmanname', 'type': 'selectUser', 'indexId': 'auditman', 'placeholder': '请输入审核人' },
    { 'name': '审核时间', 'index': 'audittime', 'type': 'datetime', 'format': 'YYYY-MM-DD HH:mm:ss', 'placeholder': '请输入审核时间' },
  ]

  public tassetid = '';
  public fileid = '';
  public forminfo = {};
  public fileinfo = {};
  public projectinfo = {};
  public techdatalists = [];

  constructor(public events: Events, public navCtrl: NavController, public storage: StorageProvider, public navParams: NavParams, public httpService: HttpServicesProvider,
    public phoneDevice: PhoneDeviceProvider, public toastCtrl:ToastController) {
    this.phoneDevice.phoneBacktoPop(this.navCtrl);

    this.tassetid = this.navParams.get('tassetid'); // 技术数据设备id
    this.fileid = this.navParams.get('fileid'); // 文件包id
  }
  ionViewWillEnter() {//当将要进入页面时触发
    this.initinfo();
  }

  initinfo() {
    var url = "/lmtagTechAsset/detail/" + this.tassetid;
    this.httpService.get(url, null, (result) => {
      this.forminfo = result.data;

      this.httpService.inittime('measuretime', this.forminfo);
      this.httpService.inittime('recordtime', this.forminfo);
      this.httpService.inittime('audittime', this.forminfo);

      this.httpService.initUserName(this.forminfo['measureman'], 'measuremanname', this.forminfo);
      this.httpService.initUserName(this.forminfo['recordman'], 'recordmanname', this.forminfo);
      this.httpService.initUserName(this.forminfo['auditman'], 'auditmanname', this.forminfo);

      this.initlist();
      this.initfileinfo();

      const qrdata = {
        type: 'lmtagfiletechdatarecord',
        text: '测试'
      }
      this.createQRcode('#assetQRcode', JSON.stringify(qrdata));
    });
  }

  initlist() {
    var url = "/lmtagTechData/list?bussid=" + this.tassetid + '&sort=seqnum&order=asc&_search=false&rows=-1&page=1&sidx=&sord=asc';
    this.httpService.get(url, null, (result) => {
      this.techdatalists = result.rows
    });
  }

  initfileinfo() {
    var url = "/lmtagFile/detail/" + this.fileid;
    this.httpService.get(url, null, (result) => {
      if (result.code == 0) {
        this.fileinfo = result.data;

        this.initprojectinfo();
      }
    });
  }

  initprojectinfo() {
    var url = "/lmtagProject/detail/" + this.fileinfo['prjid'];
    this.httpService.get(url, null, (result) => {
      if (result.code == 0) {
        this.fileinfo['projectdesc'] = result.data['projectdesc']
      }
    });
  }

  submit() {
    this.submitTechReadyState();

    this.httpService.initsavetime('measuretime', this.forminfo);
    this.httpService.initsavetime('recordtime', this.forminfo);
    this.httpService.initsavetime('audittime', this.forminfo);

    let obj = {
      master: this.forminfo
    }
    this.httpService.post('/lmtag/lmtagTechAssetService/saveMasterSlave', obj, (res) => {
      if (res.code == 0) {
        this.initinfo();
        this.toastCtrl.create({
          position: 'bottom',
          duration: 1500,
          message: '保存成功',
        }).present();
      } else {
        this.toastCtrl.create({
          position: 'bottom',
          duration: 1500,
          message: '保存失败',
        }).present();
      }
    })
  }

  // 保存测量点填报结果
  submitTechReadyState() {
    console.log(this.techdatalists);

    for (let i = 0; i < this.techdatalists.length; i++) {
      const item = this.techdatalists[i];
      let obj = {
        master: item
      }
      this.httpService.post('/lmtag/lmtagTechDataService/saveMasterSlave', obj, (res) => {
        if (res.code == 0) {
          // ...
        } else {
          this.toastCtrl.create({
            position: 'bottom',
            duration: 1500,
            message: '保存失败',
          }).present();
        }
      })
    }
    
  }

  collapseClick(index) {//区块收缩/展开
    $('page-lmtagfiletechdatarecordinfo #collapse' + index).toggle();
    $('page-lmtagfiletechdatarecordinfo .collapse_show_icon' + index).toggle();
    $('page-lmtagfiletechdatarecordinfo .collapse_hide_icon' + index).toggle();
  }

  radiorightclick(e, index, key) {
    if (e) {
      this.techdatalists[index][key] = 1;
    } else {
      this.techdatalists[index][key] = 0;
    }
    console.log(this.techdatalists[index][key]);
  }

  showscanner() {
    this.navCtrl.push('ScannerPage', { 'type': 'lmtagTechData' }, { "animate": false });
  }

  planmore() {
    this.navCtrl.push('LmtagworkorderplanbaseinfoPage', {
      type: 'edit',
      prjid: this.fileinfo['prjid']
    })
    this.events.subscribe('workorderplanbase:forminfo', (data, time) => {
      this.forminfo = data
      console.log('subscribe => ', this.forminfo);
      this.events.unsubscribe('workorderplan:forminfo');
    })
  }

  handleOpenDetail(id) {
    this.navCtrl.push('LmtagfiletechdatarecorddetailPage', {
      type: 'edit',
      id: id,
      forminfo: this.forminfo
    })
  }

  createQRcode(selector, text) {
    // console.log(`createQRcode  selector, text => `, selector, text)
    const h = $(selector).height();
    const w = $(selector).width();
    $(selector).html('');
    var qrcode = new QRCode($(selector)[0], {
      text: text,
      width: w,
      height: h,
      correctLevel: QRCode.CorrectLevel.H,
      src: 'assets/lm/lmhome/qrcode_logo.png'
    });
  }

}



